import React, { useEffect, useState } from "react";

const WeatherApi = () => {
  //ÎÇ†Ïî®
  //Î∞õÏïÑÏò¨ api ÌòïÏãùÏóê ÎßûÏ∂∞ÏÑú Í∏∞Î≥∏ state Íµ¨Ï°∞Î•º ÎßåÎì§Ïñ¥Ï§ÄÎã§.
  const [weather, setWeather] = useState({
    main: { temp: 0 },
    weather: [{ description: "ÎÇ†Ïî®" }],
  });

  //openWeatherAPI url
  const url =
    "http://api.openweathermap.org/data/2.5/weather?q=Seoul&appid=9baac3369dd75739c16d992d23f0b417&lang=kr&units=metric";
  /**
   * jsonÌòïÏãùÏúºÎ°ú ÎÇ†Ïî® api Îç∞Ïù¥ÌÑ∞Î•º Î∞õÏïÑÏò¥
   */
  const getWeather = () => {
    fetch(url)
      .then((res) => res.json())
      .then((result) => {
        //api ÌïòÎ£® ÏÇ¨Ïö©Îüâ Ï¥àÍ≥ºÎ°ú ÏûÑÏãúÎ°ú ÏïÑÎûòÏôÄ Í∞ôÏù¥ ÏÑ§Ï†ï
        //setWeather({ main: { temp: 15 }, weather: [{ description: "ÎßëÏùå" }] });
        setWeather(result);
      });
  };

  //class componentsÏùò lifecycle ÎåÄÏã† ÏÇ¨Ïö©
  useEffect(() => {
    getWeather();
    return () => {};
  }, []);
  return (
    <div className="weather">
      <div className="weatherTemp">
        <p className="weatherTempInner">{weather.main.temp.toFixed(0)}</p>
        ¬∞C
      </div>
      <div className="weatherDescription">{weather.weather[0].description}</div>
    </div>
  );
};

export default WeatherApi;
