import React from 'react';
import styled from 'styled-components';

const InfoContentDiv = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

const SelectBox = styled.select`
  width: 100%;
  color: #868e96;
  font-size: 16px;
  padding: 8px 5px;
  border: 0px;
  outline: none;
  background: url('../../image/arrow.jpg') no-repeat 95% 50%;
  border-radius: 10px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  &:hover {
    background: rgba(0, 0, 0, 0.05);
  }
`;

const InputBox = styled.input`
  background: rgba(255, 255, 255, 0);
  width: 100%;
  height: 30px;
  border: 0px;
  outline: none;
  border-radius: 10px;
  padding: 5px;
  color: #868e96;
  font-size: 16px;

  &:hover {
    background: rgba(0, 0, 0, 0.05);
  }
`;

const processLabel = [
  { label: 'ÏßÑÌñâ Ï†Ñ' },
  { label: 'ÏßÑÌñâ Ï§ë' },
  { label: 'ÏôÑÎ£å üôå' },
];

const priorityLabel = [
  { label: 'Ïö∞ÏÑ†ÏàúÏúÑ 0 üî•' },
  { label: 'Ïö∞ÏÑ†ÏàúÏúÑ 1' },
  { label: 'Ïö∞ÏÑ†ÏàúÏúÑ 2' },
  { label: 'Ïö∞ÏÑ†ÏàúÏúÑ 3' },
  { label: 'Ïö∞ÏÑ†ÏàúÏúÑ 4' },
  { label: 'Ïö∞ÏÑ†ÏàúÏúÑ 5' },
];

const InfoContent = (props: any) => {
  const { onChange, createdDate, process, priority, author } = props;
  return (
    <InfoContentDiv>
      <div>
        <InputBox type="text" value={createdDate} readOnly />
      </div>
      <div>
        <SelectBox name="process" value={process} onChange={onChange}>
          {processLabel.map((item, index) => (
            <option key={index} value={index}>
              {item.label}
            </option>
          ))}
        </SelectBox>
      </div>
      <div>
        <SelectBox name="priority" value={priority} onChange={onChange}>
          {priorityLabel.map((item, index) => (
            <option key={index} value={index}>
              {item.label}
            </option>
          ))}
        </SelectBox>
      </div>
      <div>
        <InputBox
          type="text"
          name="author"
          value={author}
          onChange={onChange}
          placeholder="ÏûëÏÑ±ÏûêÎ•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî."
        />
      </div>
    </InfoContentDiv>
  );
};

export default InfoContent;
